#parallax {
	position: fixed;
	top: -50%;
	left: -50%;
	width: 200%;
	height: 200%;
	z-index: -1;
}
#parallax img {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	min-width: 50%;
	min-height: 50%;
}
@media ( max-width: 1024px) and (orientation:portrait) {
	#parallax img {
		height: 50%;
	}
}
@media ( max-width: 1024px) and (orientation:landscape) {
	#parallax img {
		width: 50%;
	}
}
.parallax { height: 100%; }

